



















































import { ref } from '../../../../adapter-vue';
import { outsideClick } from '@tencentcloud/universal-api';
import Icon from '../../../common/Icon.vue';
import BottomPopup from '../../../common/BottomPopup/index.vue';
import { isPC, isUniFrameWork } from '../../../../utils/env';
const __sfc_main = {};
__sfc_main.props = {
  iconFile: {
    type: String,
    required: true
  },
  title: {
    type: String,
    default: ''
  },
  needDialog: {
    type: Boolean,
    default: true
  },
  iconWidth: {
    type: String,
    default: '20px'
  },
  iconHeight: {
    type: String,
    default: '20px'
  },
  // 是否需要移动端底部弹窗对话框展示, 默认为false
  // pc端无效
  needBottomPopup: {
    type: Boolean,
    default: false
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const emits = __ctx.emit;
  const showDialog = ref(false);
  const toolbarItemRef = ref();
  const dialogRef = ref();
  const toggleToolbarItem = () => {
    emits('onIconClick', dialogRef);
    if (isPC) {
      outsideClick.listen({
        domRefs: toolbarItemRef.value,
        handler: closeToolbarItem
      });
    }
    if (!props.needDialog) {
      return;
    }
    toggleDialogDisplay(!showDialog.value);
  };
  const closeToolbarItem = () => {
    showDialog.value = false;
    emits('onDialogClose', dialogRef);
  };
  const toggleDialogDisplay = (showStatus: boolean) => {
    if (showDialog.value === showStatus) {
      return;
    }
    showDialog.value = showStatus;
    switch (showStatus) {
      case true:
        emits('onDialogShow', dialogRef);
        break;
      case false:
        emits('onDialogClose', dialogRef);
    }
  };
  const onPopupClose = () => {
    showDialog.value = false;
  };
  return Object.assign({
    isPC,
    isUniFrameWork,
    props,
    showDialog,
    toolbarItemRef,
    dialogRef,
    toggleToolbarItem,
    onPopupClose
  }, {
    toggleDialogDisplay
  });
};
__sfc_main.components = Object.assign({
  Icon,
  BottomPopup
}, __sfc_main.components);
export default __sfc_main;
